<template>
  <div class="text-center">
    <soccer-games :games="requestedGames()" :isGroup="isGroup"></soccer-games>
    <loading v-if="isLoading" class="text-center"></loading>
    <no-games v-if="!isLoading && featuredGames && !featuredGames.length"></no-games>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import { Bus } from './../../EventBus'

  const SoccerGames = () => import('./GamesComponent.vue')
  const NoGames = () => import('./../Utilities/NoGames')
  const Loading = () => import('./../Utilities/LoadingIndicator')

  export default {
    name: 'FeaturedSoccer',
    props: ['isGroup'],
    components: {SoccerGames, Loading, NoGames},
    data () {
      return {
          isLoading: true,
          gamesChanged: false,
          tomorrowsGames: null
      }
    },
    mounted () {
      this.getFeaturedGames().then(() => this.isLoading = false, () => this.isLoading = false)
       Bus.$on('dateChanged', (event) => {
           if (this.featuredGames && event === 'tomorrow') {
               this.gamesChanged = !this.gamesChanged
               this.tomorrowsGames = this.$gameHelper.getTomorrowsGames(this.upcomingGames)
           } else if (this.featuredGames && event === 'today') {
               this.tomorrowsGames = this.featuredGames
           }

       })
    },
    computed: {
      ...mapGetters(['featuredGames', 'upcomingGames'])
    },
    methods: {
      ...mapActions(['getFeaturedGames']),
        requestedGames () {
          if (this.tomorrowsGames)
          return this.tomorrowsGames
          else return this.featuredGames
        }

    }
  }
</script>

